/**
 * Sing In / Sign Out link directive - generates link which automatically switches between Sign In / Sign Out modes
 */
angular.module('deskworks.signInLink', [
  'deskworks.state'
])

  .directive('signInLink', function(state) {
    return {
      template:
        '<a ng-href="{{state.getLoggedUserId() ? \'#/sign-out\' : \'#/sign-in\'}}"' +
          ' ng-click="signInOrOut($event)" ff-spin="spin" snap-close class="sidebar-item" ng-class="{ active: active }">' +
          '<span ng-if="!state.getLoggedUserId()" translate="SIDEBAR.SIGN_IN"></span>' +
          '<span ng-if="state.getLoggedUserId()" translate="SIDEBAR.SIGN_OUT"></span>' +
        '</a>',
      restrict: 'E',
      scope: true,
      link: function ($scope, $element, $attrs) {
        $scope.state = state;

        $attrs.$observe('active', function(val) {
          $scope.active = val === 'true';
        });

        /**
         * Sign In or Sign Out action
         */
        $scope.signInOrOut = function($event) {
          $event.stopPropagation();
          $event.preventDefault();
          state.resetParams();
          state.setPath(state.getLoggedUserId() ? '/sign-out' : '/sign-in');
        };
      }
    };
  });
