/**
 * White-label service for customizing header and footer for each client individually.
 */

export default angular.module('deskworks.whitelabel', ['getColorContrast'])

  .provider('whitelabel', function() {
    return {
      setProperty: (prop, val) => {
        const styles = window.getComputedStyle(document.documentElement);
        if (!styles.getPropertyValue('--dynamic-white-label')) { // if set, vars are stored in database
          document.documentElement.style.setProperty(prop, val);
        }
      },

      $get: function(getColorContrast) {
        updateTextOnBrandColor(); // initial recalc text-on-brand-color

        return {
          setProperty: (prop, val) => {
            document.documentElement.style.setProperty(prop, val);
            if (prop === '--brand-color') updateTextOnBrandColor();
          },
          getProperty: (prop) => {
            const styles = window.getComputedStyle(document.documentElement);
            const val = styles.getPropertyValue(prop);
            if (prop === '--elastic-banner' && !val) return 'true'; // default value
            return val && val.trim();
          },
        };

        function updateTextOnBrandColor() {
          const styles = window.getComputedStyle(document.documentElement);
          const color = getColorContrast(styles.getPropertyValue('--brand-color').trim()) < 170 ? '#fff' : '#000';
          document.documentElement.style.setProperty('--text-on-brand-color', color);
        }
      }
    };
  });
