export default angular.module('getColorContrast', [])

  .factory('getColorContrast', () => {
    return (color) => {
      let r, g, b, a;
      if (color.indexOf('#') >= 0) { // #f088d1 or #f088d1ff
        if (color.length < 7) { // #f0f or #f0ff
          color = [...color].map(char => char === '#' ? char : char + char).join('');
        }
        [r, g, b, a] = [1, 3, 5, 7].map(i => parseInt(color.substr(i, 2), 16));
        a = isNaN(a) ? a = 1 : a / 255;
      } else { // rgba(255, 255, 255, .75)
        const match = color.match(/^rgba\s*\(\s*(\d+)\s*,\s*(\d+)\s*,\s*(\d+)\s*,\s*(0*\.?\d+)\s*\)$/i);
        [r, g, b, a] = match.slice(1);
        [r, g, b] = [r, g, b].map(i => parseInt(i));
        a = parseFloat(a);
      }

      return ((r * 299) + (g * 587) + (b * 114))  / 1000; // YIQ ratio
    };
  });
